/* eslint-disable react/no-array-index-key */
import React from 'react';
import IconCard from '../../../components/cards/IconCard';
import GlideComponent from '../../../components/carousel/GlideComponent';

const DashboardDataCarousel = ({ className = 'icon-cards-row', data }) => {
  const hackData = [
    {
      title: 'dashboards.total-hackathons',
      icon: 'iconsminds-clock',
      value: data.length,
    },
    {
      title: 'dashboards.total-submissions',
      icon: 'iconsminds-arrow-refresh',
      value: data.filter((item) => item.submit_status === 1).length,
    },
    {
      title: 'dashboards.total-created',
      icon: 'iconsminds-basket-coins',
      value: data.length,
    },
    {
      title: 'dashboards.on-going-hackathons',
      icon: 'iconsminds-clock',
      value: data.filter((item) => item.submit_status === 0).length,
    },
  ];

  return (
    <div className={className}>
      <GlideComponent
        settings={{
          gap: 5,
          perView: 4,
          type: 'carousel',
          breakpoints: {
            320: { perView: 1 },
            576: { perView: 2 },
            1600: { perView: 3 },
            1800: { perView: 4 },
          },
          hideNav: true,
        }}
      >
        {hackData.map((item, index) => {
          return (
            <div key={`icon_card_${index}`}>
              <IconCard {...item} className="mb-4" />
            </div>
          );
        })}
      </GlideComponent>
    </div>
  );
};
export default DashboardDataCarousel;
