import React from 'react';
import { ContextMenu, MenuItem } from 'react-contextmenu';

const DashboardContextMenu = ({ onContextMenuClick }) => {
  return (
    <ContextMenu id="same_unique_identifier" style={{ borderRadius: '16px' }}>
      <MenuItem onClick={onContextMenuClick} data={{ action: 'refresh' }}>
        <i className="simple-icon-refresh" /> <span> Refresh</span>
      </MenuItem>
    </ContextMenu>
  );
};

export default DashboardContextMenu;
