import React, { useEffect } from 'react';
import { /* Card, CardBody, */ Row } from 'reactstrap';
import { connect } from 'react-redux';
import { ContextMenuTrigger } from 'react-contextmenu';

import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
// import Bar from '../../../components/charts/Bar';

import DashboardDataCarousel from './DashboardDataCarousel';

// import { BarChartData } from '../../../data/charts';
import { getCurrentUser, log } from '../../../helpers/Utils';

import HackCard from '../../../containers/application/HackathonCard';
import { getAllHackathons } from '../../../redux/actions';
import DashboardContextMenu from './components/context-menu';

const Dashboard = ({
  match,
  accessToken,
  organizerHackathons,
  getAllHackathonsAction,
  loading,
  error,
}) => {
  const { id } = getCurrentUser();

  useEffect(() => {
    getAllHackathonsAction(accessToken, id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (loading) return <div className="loading" />;

  if (error) log(error);

  const onContextMenuClick = (e, item) => {
    log('onContextMenuClick - action : ', item.action);

    switch (item.action) {
      case 'refresh':
        getAllHackathonsAction(accessToken, id);
        break;

      default:
        break;
    }
  };

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.dashboard" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <ContextMenuTrigger id="same_unique_identifier">
        <Row>
          <Colxx xxs="12" md="8">
            <DashboardDataCarousel data={organizerHackathons} />
          </Colxx>

          {/* <Colxx xxs="12" md="8" className="mb-4">
            <Card>
              <CardBody>
                <div className="chart-container">
                  <Bar data={BarChartData} shadow />
                </div>
              </CardBody>
            </Card>
          </Colxx> */}
        </Row>

        <Row>
          <Colxx xxs="12">
            <h1>Drafts</h1>
            <Separator className="mb-5" />
          </Colxx>
        </Row>

        <Row>
          {organizerHackathons.map((item) => {
            if(item.name !== undefined)
            {
              return(
            <Colxx xxs="12" md="4" key={item.id} className="mb-4">
              <HackCard data={item} showEdit={item.submit_status === 0} />
            </Colxx>);
            }
            return null;
          })}
        </Row>
      </ContextMenuTrigger>
      <DashboardContextMenu onContextMenuClick={onContextMenuClick} />
    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { organizerHackathons, loading, error } = hackathonData;
  return { accessToken, organizerHackathons, loading, error };
};

export default connect(mapStateToProps, {
  getAllHackathonsAction: getAllHackathons,
})(Dashboard);
