/* eslint-disable camelcase */
import React from 'react';
import { useHistory } from 'react-router-dom';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardSubtitle,
  CardText,
  CardTitle,
  NavLink,
  Row,
} from 'reactstrap';
import { Separator } from '../../components/common/CustomBootstrap';
import { adminRoot } from '../../constants/defaultValues';
import IntlMessages from '../../helpers/IntlMessages';

const getStatusColor = (status) => {
  if (status === 0) return 'outline-success';
  if (status === 1) return 'outline-warning';
  if (status === 'COMPLETED') return 'outline-danger';
  return '';
};

const getStatusText = (status) => {
  if (status === 0) return 'Draft';
  if (status === 1) return 'Under Review';
  if (status === 2) return 'Approved';
  return '';
};

const getTypeText = (status) => {
  if (status === 0) return 'Online';
  if (status === 1) return 'Blender';
  if (status === 2) return 'Offiline';
  return '';
};

const HackCard = ({ data, showEdit = true }) => {
  const {
    id,
    name,
    tagline,
    submit_status,
    hackathon_website,
    hackathon_start,
    application_end,
    hackathon_type_id,
    otherArray,
    
  } = data;
  console.log(data);
  const history = useHistory();

  return (
    <Card>
      <CardBody>
        <CardTitle>
          <p className="h2 lead">
            <a
              href="https://google.com"
              target="_blank"
              rel="noreferrer"
              className="new-tab"
            >
              {name}
            </a>
          </p>
          <CardSubtitle className="text-small">{tagline}</CardSubtitle>
        </CardTitle>

        <Row className="justify-content-center">
          {otherArray.sdg_goals.map((item) => (
            <img
              key={item.id}
              src={`/assets/img/sdg/${item.id}.png`}
              alt={item.name}
              className="list-thumbnail align-self-center m-1 small hackcard-icon"
            />
          ))}

          <div className="vertical" style={{ height: '45px' }} />

          {otherArray.$mega_trends.map((item) => (
            <img
              key={item.id}
              src={`/assets/img/mega-trends/${item.id}.png`}
              alt={item.name}
              className="list-thumbnail align-self-center m-1 small hackcard-icon"
            />
          ))}
        </Row>

        <Separator className="my-4" />

        <CardTitle>
          <IntlMessages id="hackcard.application" />
          <CardText>{application_end}</CardText>
        </CardTitle>

        <CardTitle>
          <IntlMessages id="hackcard.hackthon" />
          <CardText>{hackathon_start}</CardText>
        </CardTitle>

        <Separator className="mb-4" />

        <Badge
          color={getStatusColor(submit_status)}
          pill
          outline
          className="mb-3 mr-3"
        >
          {getStatusText(submit_status)}
        </Badge>

        <Badge color="outline-primary" pill className="mb-3">
          {getTypeText(hackathon_type_id)}
        </Badge>

        <CardSubtitle className="text-small">
          <NavLink href={hackathon_website} target="_blank" rel="noreferrer">
            <IntlMessages id="hackcard.view-more" />
          </NavLink>
        </CardSubtitle>

        <Row className="justify-content-center">
          {showEdit && (
            <Button
              className="m-1"
              outline
              onClick={() => {
                history.push(`${adminRoot}/challenges/basics/${id}`);
              }}
            >
              <IntlMessages id="hackcard.edit-button" />
            </Button>
          )}
          <Button
            className="m-1"
            color="primary"
            onClick={() => {
              history.push(`${adminRoot}/challenges/details/${id}`);
            }}
          >
            <IntlMessages id="hackcard.view-details-button" />
          </Button>
        </Row>

        {/* <pre>{JSON.stringify(data, null, 2)}</pre> */}
      </CardBody>
    </Card>
  );
};

export default HackCard;
